$principal: #AC6000;
$principal-light: #B27C00;
$secundario: #A50401;
$bg-brand: rgb(240, 215, 0);
$bg-light: #ECE1B5;

$dark: #111;
$texto: $dark;
$white: #fff;

$font-header: 'Poppins', sans-serif;
$font-body: 'Roboto', sans-serif;

* {
    outline: none;
}

html {
    font-size: 16px;
}

body {
	font-family: 'Poppins', sans-serif;
	color: $texto;
}

a {
    color: $principal;
    &:hover {
        text-decoration: none;
        color: $principal-light;
    }
}

button,input {
	outline:0;
}

.gt-main {
    background: url("../img/bg.jpg") no-repeat center top;
    color: $principal;
    background-size: cover;
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
}

.gt-main__encabezado {
    background: $bg-brand !important;
    font-family: $font-header;
    font-size:1.2rem;
    letter-spacing: 1px;
    color: $principal;
    padding: .5rem 1rem;
    /*border-bottom: 2px solid $dark;*/

    .gt-btn-menu {
        @media (max-width: 420px) {
            span {
                display: none;
            }
        }
    }

    .gt-page-title {
        flex: 1 1 auto;
        text-align: center;
        h3 {
            font-weight: 700;
            color: $principal;
            margin-bottom: 0px;
            @media(max-width: 420px) {
                font-size:1.3rem;
            }
        }
    }
}

.gt-main__content {
    flex:1 1 auto;
    overflow: auto;
    width: 100%;
    padding: 16px 32px;
    margin:0 auto;
}

.paso {
    &.active {
        animation: scale-display .3s;
        display: block;
    }

    &.out {
        animation: scale-display--reversed .3s;
        animation-fill-mode:forwards;
        display: none !important;
    }
}

@keyframes scale-display {
    0% {
        display: none;
        opacity: 0;
    }

    100% {
        opacity: 1;
        display: block;
    }
}

@keyframes scale-display--reversed {
	0% {
		display: block;
		opacity: 1;
	}

	100% {
		display: none;
		opacity: 0;
	}
}

.gt-main__footer {
    padding: 16px 32px;
    background: $white;
    display: flex;
    justify-content: space-between;
    align-items: center;

    .gt-next-page {
        > a:first-child {
            position: relative;
            &::before{
                content: '|';
                top: 0px;
                right: -6px;
                position: absolute;
                width: 1px;
                color: gray;
            }
        }

    } 

    @media(max-width: 420px) {
        justify-content: center;
        .gt-next-page {
            display: none;
        }
    }
}

.gt-controls {
    button {
        width: 40px;
        height: 40px;
        line-height: 40px;
        text-align: center;
        border-radius: 50%;
        margin-right: 24px;
        outline: 0;
        background: #ddd;
        color: $secundario;
        transition: all .35s ease-in-out;
        border: 0px;
        /*border: 2px solid #38424F;*/
        cursor: pointer;
        &:hover {
            transform: scale(1.1);
        }
    }
}

.sidenav {
    height: 100%; /* 100% Full-height */
    width: 0; /* 0 width - change this with JavaScript */
    position: fixed; /* Stay in place */
    z-index: 1; /* Stay on top */
    top: 0;
    left: 0;
    background-color: $white; /* Black*/
    overflow-x: hidden; /* Disable horizontal scroll */
    padding-top: 50px; /* Place content 60px from the top */
    transition: 0.5s; /* 0.5 second transition effect to slide in the sidenav */

    ul.sidenav-menu {
        padding-left: 0px;

        > li > a {
            font-family: $font-header;
            font-size: .9rem;
            color: $principal;
            font-weight: 300;
            &:hover{
                background: rgba(236,225,181,0.7);
            }
        }
    }
}

/* The navigation menu links */
.sidenav-menu a {
    padding: 8px 8px 8px 32px;
    text-decoration: none;
    font-size: 25px;
    color: #818181;
    display: block;
    border-top: 1px solid #ddd; 
    transition: 0.3s;
}

/* Position and style the close button (top right corner) */
.sidenav .closebtn {
    position: absolute;
    top: 0;
    right: 25px;
    font-size: 36px;
    margin-left: 50px;
    transition: all .35s ease-in-out;
    &:hover {
        transform: rotate(180deg);
    }
}

/* Style page content - use this if you want to push the page content to the right when you open the side navigation */
#main {
    transition: margin-left .5s;
}

/* On smaller screens, where height is less than 450px, change the style of the sidenav (less padding and a smaller font size) */
@media screen and (max-height: 450px) {
    .sidenav {padding-top: 15px;}
    .sidenav a {font-size: 18px;}
}

/*=============================================
=            PÁGINA INTERMEDIACION            =
=============================================*/

#page-intermediacion {

    .gt-main__footer {
        //no existe anterior por eso no ponemos borde a la derecha
        //lo colocamos de blanco para que no se vea.
        .gt-next-page {
            > a:first-child {
                position: relative;
                &::before{
                    color: #fff;
                }
            }

        } 
    }
}


#gt-page-intermediacion {

    .container {
        max-width: 1260px;
    }

    .gt-beneficios {
        @media (max-width: 812px) { 
            flex-direction: column;

            .flecha {
                display: none;
            }
        }
        @media (max-width: 420px) {  
            //cambiamos el display flex por display block
            display: block !important;
            overflow: auto;
        }
    }

    .gt-beneficios-bloque {
        /*background: url("../img/engranaje.png") no-repeat center top;
        background-size: cover;*/
        /*background-image: linear-gradient(120deg, #f6d365 0%, #fda085 100%);*/
        //background-image: linear-gradient(to top, #f3e7e9 0%, #e3eeff 99%, #e3eeff 100%);
        background-image: linear-gradient(to top, #e6b980 0%, #eacda3 100%);
        border-radius: 50%;
        width: 300px;
        height: 300px;
        display: flex;
        justify-content: center;
        align-items: center;
        margin-right: 24px;
        position: relative;
        
        @media (max-width: 420px) {  
            margin-bottom: 24px;
        }

        @media (max-width: 1280px) {  
            width: 220px;
            height: 220px;
        }

        .inner {
            width: 80%;
            /*@media (max-width: 1280px) {  
                width: 120px;
            }*/
        }

        .gt-campo {
            max-width: 100%;
        }

        ul {
            margin-top: 12px;
            li {
                list-style-type: none;
                color: $dark;
                text-align: center;
                font-size: 0.8rem;
                @media (max-width: 1280px) {  
                    font-size: 0.7rem;
                }
            }
        }

        &:last-child {
            margin-right: 0px;
        }

        .flecha {
            position: absolute;
            width: 286px;
            height: 202px;
            top: -140px;
            left: -180px;
            background: url('../img/flecha.png') no-repeat top left;
            @media (max-width: 1280px) {  
                width: 200px;
                height: 141px;
                background-size: cover;
                top: -100px;
                left: -125px;
            }
        }
    }
}

.big-title {
    font-weight: 700;
    color: $secundario;
}

.flecha-bajar a {
    display: inline-block;
    color: $secundario;
    font-size: 4rem;
    animation: flecha-animada 2s ease-in-out infinite;
}

@keyframes flecha-animada {
    from {
        transform: translateY(0);
    }
    50% {
        transform: translateY(6px);
    }
    to {
        transform: translateY(0);
    }
}

.animar-boton {
    animation: boton-animado 2s ease-in-out infinite;
}

@keyframes boton-animado {
    from {
        transform: scale(1);
    }
    50% {
        transform: scale(1.1);
    }
    to {
        transform: scale(1.2);
    }
}

/*=============================================
=           ASESORIA PERSONALIZADA           =
=============================================*/
#gt-page-asesor {

    .texto {
        flex: 1 1 60%;
        display: flex;
        justify-content: space-between;
        flex-direction: column;
        height: 100%;
        @media(max-width: 420px) {
            flex: 1 1 auto;
            h4 {
                font-size: 1.2rem;
            }
        }
    }
    .imagenes {
        display: none;

        @media(min-width: 768px) {
            flex: 1 1 40%;

            img {
                width: 240px;
            }
        }

    }
}

/*=============================================
=           PORTAFOLIO DE SERVICIOS           =
=============================================*/
#gt-page-portafolio {

    .texto {
        display: flex;
        justify-content: space-between;
        flex-direction: column;
        height: 100%;
        @media(max-width: 420px) {
            flex: 1 1 auto;
            h4 {
                font-size: 1.2rem;
            }
        }
    }

}

.modal {
    .item {
        border-radius: 12px;
    }
}